import react, {Component} from 'react';
import {Container, Title, Field, Control, FieldBody, FieldLabel, Label, Input,
         Icon} from'bloomer'

const logos ={
    backgroundColor: '#009688',
    //height: '50px',
     padding: '4px',
    color:'white'
}

export default class Login extends Component{

    render(){
        return(
            <div style={logos}>
            <Container>
            <p isSize={5} style={{color:'white'}}>Please Login</p>
            <Field isHorizontal>
<FieldLabel isNormal>

    <Label>From</Label>
</FieldLabel>
<FieldBody>
    <Field isGrouped>
        <Control isExpanded hasIcons='left'>
            <Input placeholder='Name' />
            <Icon isSize='small' isAlign='left'><span className="fa fa-user" aria-hidden="true"/></Icon>
        </Control>
    </Field>
    <Field>
        <Control hasIcons={['left', 'right']}>
            <Input isColor='success' placeholder='Email' value='john@wick.com' />
            <Icon isSize='small' isAlign='left'><span className='fa fa-envelope' /></Icon>
            <Icon isSize='small' isAlign='right'><span className='fa fa-check' /></Icon>
        </Control>
    </Field>
</FieldBody>
</Field>


               
               
                </Container></div>
        )
    }

}


